import React, {useContext} from "react";
import styled from "styled-components";
import {Navbar, Nav} from "react-bootstrap";
import {Link} from "react-router-dom";
import {Context} from "../../Contex";


const NavbarCom = () => {
    const {cardArray} = useContext(Context)
    return (
        <Container>
            <Navbar collapseOnSelect expand="lg">
                <div className="container">
                    <div className="navbar-brand">Furniture Shop</div>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="m-auto">
                            <Link className="nav-link" to="/">Home</Link>
                            <Link className="nav-link" to="/About">About</Link>
                            <Link className="nav-link" to="/Products">Products</Link>
                        </Nav>
                        <Nav className="ml-auto">
                            <Link className="nav-link cards-width" to="/shoppingCard">Cards<i
                                className="fas fa-shopping-cart"/><sup className="sup">{cardArray.length}</sup></Link>
                            <Link className="nav-link cards-width" to="/resgistor">Login <i className="fas fa-user-plus"/></Link>
                        </Nav>
                    </Navbar.Collapse>
                </div>
            </Navbar>
        </Container>
    )
}

const Container = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 99 !important;
  background: linear-gradient(180deg, #000000 0%, rgba(14, 14, 28, 0.77046) 71.05%, rgba(17, 16, 33, 0.0001) 100%);
  min-height: 80px !important;

  .container {
    max-width: 70% !important;
    margin: auto !important;

    .navbar-brand {
      color: white !important;
      transition: 0.5s ease;
      font-size: 39px;
      font-weight: bold;

      :hover {
        transform: scale(1.1);
      }
    }

    .navbar-toggler-icon {
      background-color: white !important;
      border-radius: 5px;
    }

    .nav-link {
      color: white !important;
      font-size: 20px !important;
      margin-right: 30px !important;
      transition: 0.5s ease;

      .fa-shopping-cart, .fa-user-plus {
        margin-left: 8px;
      }

      :hover {
        transform: scale(1.1);
      }
    }

    .nav-link:last-child {
      margin-right: 0 !important;
    }

    .cards-width {
      width: 125px;
      .sup {
        padding: 0 6px;
        border-radius: 50%;
        background-color: red;
        color: #FFFFFF;
      }
    }

    //  Media Style
    @media (max-width: 600px) {
      .navbar-brand {
        font-size: 20px !important;
      }

      .nav-link {
        font-size: 18px !important;
      }

      .navbar-toggler-icon {
        width: 1rem !important;
        height: 1rem !important;
        border-radius: 5px;
      }
    }
    @media (max-width: 410px) {
      .navbar-brand {
        font-size: 17px !important;
      }

      .nav-link {
        font-size: 16px !important;
      }

      .navbar-toggler-icon {
        width: 0.6rem !important;
        height: 0.6rem !important;
        border-radius: 2px;
      }
    }
    @media (max-width: 340px) {
      .navbar-brand {
        font-size: 15px !important;
      }

      .nav-link {
        font-size: 13px !important;
      }
    }
  }

`

export default NavbarCom;