import React, {useContext} from 'react';
import styled from "styled-components";
import {Context} from "../../Contex";
import ProductCom from "../../components/ProductCom/ProductCom";

const Product = () => {
    const {array,data,setArray} = useContext(Context)
    /*  */
    const searchByName =(e)=>{
      let name = e.target.value.toLowerCase()
      const newArray = data.filter(el => {
        return el.name.toLowerCase().includes(name)
      })
      setArray(newArray)
    }

    const handlerFilterCategory= (e) =>{
      const category = e.target.value
      const newArray = data.filter(el => {
        return el.company.toLowerCase().includes(category.toLowerCase())
      })
      setArray(newArray)
      console.log(newArray)
    }
    return (
        <Container>
            <div className="container">
                <div className="product-nav">
                    <form action="https://echo.htmlacademy.ru/" method="post">
                        <select onChange={handlerFilterCategory} name="select" id="select">
                            <option value="marcos">Marcos</option>
                            <option value="liddy">Liddy</option>
                            <option value="ikea">Ikea</option>
                        </select>
                        <label htmlFor="search">
                            <input onChange={searchByName} type="search" name="search" id="search" placeholder="Search"/>
                            <i className="fas fa-search"/>
                        </label>
                    </form>
                </div>
                <div className="product-row">
                        {array.map(el => {
                            return <ProductCom el={el}/>
                        })}
                </div>
            </div>
        </Container>
    );
};

const Container = styled.div`
  .container {
    max-width: 70% !important;
    margin: auto !important;
    .product-nav{
      width: 100%;
      margin-top: 150px;
      padding: 0 18px;
      form{
        display: grid;
        grid-gap: 20px;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        display: flex;
        justify-content: space-between;
        select{
          width: 200px;
          border-radius: 5px;
        }
        label{
          width: 200px;
          background: white;
          border: 2px solid #54D6FF;
          border-radius: 19px;
          height: 40px;
          outline: none;
          input{
            border: none;
            outline: none;
            background: transparent;
            padding: 5px 10px;
            width: 80%;
            ::placeholder{
              color: #000F26;
              font-size: 18px;
            }
          }
          .fa-search{
            color: black;
            padding-top: 6px;
          }
        }
      }
    }
    .product-row {
      margin-top: 50px;
      display: grid;
      grid-gap: 20px;
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    }
  }
`

export default Product;