import React, {createContext, useState, useEffect} from "react";

const Context = createContext();

const ContextProvider = ({children}) => {
    const url = "https://course-api.com/react-store-products"
    const [data, setData] = useState([])
    const [array, setArray] = useState([])

    const [cardArray, setCardArray] = useState([])
    const getData = async () => {
        const response = await fetch(url)
        const data = await response.json()
        setData(data)
        setArray(data)
    }
    useEffect(() => {
        getData()
        console.log('Hello world')
    }, [])


    const toggleFavorite = (id) => {
        const Favorite = array.map(el => {
            if (el.id === id) {
                return {
                    ...el,
                    isFavorite: !el.isFavorite
                }
            }
            return el
        })
        setArray(Favorite);
    }

    const AddCard = (newItem) => {
        setCardArray([...cardArray, newItem])
    }
    const RemoveCard = (id) => {
        let newArray = cardArray.filter((el) => {
            return el.id !== id
        })
        setCardArray(newArray)
    }

    return (
        <div>
            <Context.Provider value={{array, setArray, data, setData, AddCard, RemoveCard, cardArray, toggleFavorite}}>
                {children}
            </Context.Provider>
        </div>
    )
}

export {ContextProvider, Context};