import React, {useContext, useState} from 'react';
import {Context} from "../../Contex";
import styled from "styled-components";

const ShoppingCardPhoto = ({el}) => {

    const [hover, setHover] = useState(false)
    const {RemoveCard} = useContext(Context)

    return (
        <Container onMouseLeave={() => setHover(false)} onMouseEnter={() => setHover(true)}>
            {hover && (
                <i onClick={() => {RemoveCard(el.id)}} className="fas fa-trash-alt fa-2x"/>
            )}
            <div className="card border-0 bg-transparent">
                <div className="card-header border-0">
                    <img className="image" src={el.image} alt="Photo"/>
                </div>
                <div className="card-body border-0 d-flex align-items-center justify-content-between">
                    <h4>{el.name}</h4>
                    <p>{el.price}.00$</p>
                </div>
            </div>
        </Container>
    )
};

const Container = styled.div`
  position: relative;
    .card-header{
      overflow: hidden;
      border-radius: 10px;
      .image {
        width: 100%;
        height: 300px;
        object-fit: cover;
        transition: 0.6s;
        border-radius: 10px;

        :hover {
          transform: scale(1.2);
        }
      }
    }
  .card-body {
    h4 {
      text-transform: capitalize;
    }
  }
  .fa-trash-alt {
    position: absolute;
    top: 30px;
    right: 30px;
    z-index: 2 !important;
    color: red;
  }
`

export default ShoppingCardPhoto;