import React from 'react';
import styled from "styled-components";
import '../../GeneralStyles/style.scss'
import {Link} from "react-router-dom";


const HeaderCom = () => {
    return (
        <Container>
            <div className="header-img">
                <div className="header-opacity">
                    <div className="container">
                        <div className="header-text">
                            <h3>Online Shop</h3>
                            <h1>New Furniture</h1>
                            <p>
                                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Autem impedit ipsam
                                tenetur.
                            </p>
                            <p>
                                Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                            </p>
                            <Link to="/About"><button className="btn header-btn">Read More</button></Link>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    );
};

const Container = styled.div`
  .header-img {
    .header-opacity {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);

      .container {
        max-width: 70%;
        margin: auto;

        .header-text {
          display: flex;
          flex-direction: column;
          width: 100%;
          height: 90vh;
          align-items: center !important;
          justify-content: center !important;
          line-height: 2rem;

          h3 {
            font-size: 40px;
          }

          h1 {
            font-size: 65px;
          }

          p {
            font-size: 20px;
          }

          .header-btn {
            padding: 7px 50px;
            background-color: transparent;
            color: white;
            font-size: 18px;
            outline: none !important;
            border: 2px solid #5b5bff;
            border-radius: 10px;
            transition: 0.5s ease-in-out;
            :hover {
              transform: scale(1.1);
            }
          }
        }
      }
    }
  }

  //  Media Style
  @media (max-width: 1400px) {
    .header-img {
      height: 60vh;

      .header-opacity {
        .container {
          .header-text {
            line-height: 1.2rem;
            height: 60vh;

            h3 {
              font-size: 30px;
            }

            h1 {
              font-size: 50px;
            }

            p {
              text-align: center;
              font-size: 18px;
            }

            .header-btn {
              padding: 5px 40px;
              font-size: 18px;
            }
          }
        }
      }
    }
  }
  @media (max-width: 750px) {
    .header-img {
      height: 60vh;

      .header-opacity {
        .container {
          .header-text {
            line-height: 1rem;
            height: 60vh;

            h3 {
              font-size: 20px;
            }

            h1 {
              font-size: 30px;
            }

            p {
              //text-align: center;
              //font-size: 15px;
              display: none;
            }

            .header-btn {
              padding: 7px 30px;
              font-size: 15px;
            }
          }
        }
      }
    }
  }
`

export default HeaderCom;