import React, {useContext} from 'react'
import {useParams} from 'react-router'
import {Context} from './Contex'
import styled from 'styled-components'

const Detail = () => {
    const {array} = useContext(Context)
    const params = useParams()
    const data = array.find(el => el.id === params.id)
    return (
        <Container>
            <div className="container">
                <div className="detail-Row">
                    <div className="detail-col">
                        <div className="card border-0 bg-transparent">
                            <img src={data.image} alt="Photo"/>
                        </div>
                    </div>
                    <div className="detail-col">
                        <div className="card border-0 bg-transparent">
                            <h1 className="h1">{data.name}</h1>
                            <p className="p lead" style={{marginTop:"30px"}}>{data.description}</p>
                            <div className="d-flex justify-content-between mt-3">
                                <h3 className="h3">Company: { data.company}</h3>
                                <h3 className="h3">Category: { data.category}</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Container>
    )
}

const Container = styled.div`
  .container {
    max-width: 70% !important;
    margin: auto !important;
    .detail-Row{
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
      grid-gap: 80px;
      margin-top: 250px;
      img{
        max-width: 600px;
        height: 450px;
        object-fit: cover;
        border-radius: 15px;
      }
      .detail-col{
        .card{
          .h1,.h3{
            text-transform: capitalize;
          }
        }
      }
    }
  }
`

export default Detail;
