import React from "react";
import NavbarCom from "./components/NavbarCom/NavbarCom.js";
import { Switch, Route } from 'react-router-dom';
import Home from "./pages/Home/Home";
import About from "./pages/About/About";
import ShoppingCard from "./components/ShoppingCard/ShoppingCard";
import Product from "./pages/Product/Product";
import Detail from "./Detail.js";
import Registor from './pages/Login/Resgisor'
import SignInUp from './pages/Login/SignInUp'
import FooterCom from './components/FooterCom/FooterCom.js'

function App() {
    return (
        <div className="App">
            <NavbarCom />
            <Switch>
                <Route exact path="/">
                    <Home />
                </Route>
                <Route exact path="/About">
                    <About />
                </Route>
                <Route exact path="/Products">
                    <Product />
                </Route>
                <Route exact path="/Products/:id">
                    <Detail />
                </Route>
                <Route exact path="/shoppingCard">
                    <ShoppingCard />
                </Route>
                <Route exact path='/resgistor'>
                    <Registor />
                </Route>
                <Route exact path='/signupin'>
                    <SignInUp />
                </Route>
            </Switch>
        </div>
    );
}

export default App;
