import React from 'react';
import styled from "styled-components";
import Image from '../../image/design-img.jpg'
import FooterCom from "../FooterCom/FooterCom";

const DesignCom = () => {
    return (
        <Container>
            <div className="container">
                <div className="design-row">
                    <div className="design-col">
                        <div className="card border-0 bg-transparent">
                            <img className="image" src={Image} alt="Photo"/>
                        </div>
                    </div>
                    <div className="design-col">
                        <div className="card border-0 bg-transparent">
                            <div className="card-header border-0 bg-transparent">
                                <h1 className="design-h1">Lorem ipsum</h1>
                                <p className="textp">Lorem ipsum dolor sit amet, consectetur adipisicing elit. A asperiores atque debitis
                                    deserunt earum eius eligendi error, et facere in iusto, necessitatibus non odit
                                    optio perspiciatis quod tempora temporibus voluptatibus! A ad atque excepturi fugiat
                                    harum illum molestias, nam, nobis odio, perferendis perspiciatis porro quia
                                    recusandae sequi temporibus unde voluptate! Beatae consequuntur, debitis deleniti,
                                    esse incidunt, inventore ipsum laborum nam natus numquam qui sapiente sint
                                    voluptatibus! Accusamus amet, aperiam dolores eius et fugit, id in laboriosam magni
                                    minima officia quidem, quo rerum ullam vel. Aliquid amet, beatae, commodi doloribus
                                    ducimus fuga incidunt laboriosam molestias quam quibusdam, recusandae totam ut
                                    voluptate!</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <FooterCom/>
        </Container>
    );
};

const Container = styled.div`
  .container {
    max-width: 70% !important;
    margin: auto !important;

    .design-row {
      margin-top: 200px;
      display: grid;
      grid-gap: 60px;
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
      grid-auto-rows: 3fr;

      .design-col {
        .image {
          width: 100%;
          height: 600px;
          object-fit: cover !important;
          border-radius: 15px;
          border: 7px double white;
        }

        .card {
          .card-header {
            .design-h1 {
              font-size: 40px;
            }
            .textp{
              font-size: 24px;
            }
          }
        }
      }
    }
  }
`

export default DesignCom;