import React,{useContext} from 'react';
import HeaderProductsDetail from "./HeaderProductsDetail";
import {Context} from "../../Contex";
import styled from "styled-components";
import {Link} from "react-router-dom";


const HeaderProductsCom = () => {
    const {array} = useContext(Context)
    return (
        <Container>
            <div className="container">
                <div className="row">
                    <h2 style={{ textAlign: "center"}}>Featured Products</h2>
                    <div className="line"> </div>
                </div>
                <div className="header-row">
                    {array.slice(4, 10).map(el=>{
                        return(
                            <div className="header-col">
                                <HeaderProductsDetail el={el}/>
                            </div>
                        )
                    })}
                </div>
                <div className="d-flex justify-content-center">
                    <Link to="/Products"><button className="btn bt text-uppercase">All Products</button></Link>
                </div>
            </div>
        </Container>
    );
};

const Container = styled.div`
    .container{
        max-width: 70%;
        margin: auto;
      .row{
        padding: 180px 0 30px;
        .line {
          width: 90px;
          height: 4px;
          background-color: #ab7a5f;
          margin: 7px auto;
        }
      }
      .header-row{
        display: grid;
        grid-gap: 20px;
        grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
        grid-auto-rows: 1fr;
      }
      .d-flex{
        .bt {
          width: 180px;
          margin: 40px auto !important;
          text-align: center;
          background-color: #ab7a5f;
          border-radius: 10px;
          color: #eaded7;
          padding: .450rem .95rem;
          letter-spacing: .1rem;
          box-shadow: -1px 3px 15px 7px rgba(255,160,88,0.6);
          font-weight: 600;
        } 
      }
    }

`
export default HeaderProductsCom;