import React, {useState, useContext} from 'react';
import styled from "styled-components";
import {Context} from "../../Contex";

const HeaderProductsDetail = ({el}) => {
    const [hovered, setHovered] = useState(false)
    const {toggleFavorite, AddCard, cardArray, RemoveCard} = useContext(Context)

    function Heart() {
        if (el.isFavorite) {
            return <i onClick={() => toggleFavorite(el.id)} className="fas fa-heart fa-2x"/>
        } else {
            return <i onClick={() => toggleFavorite(el.id)} className="far fa-heart fa-2x"/>
        }
    }

    const ShopCards = () => {
        const plusArray = cardArray.find(cart => cart.id === el.id)
        if (plusArray) {
            return <i onClick={() => RemoveCard(el.id)} className="fas fa-shopping-cart fa-2x"/>
        } else {
            return <i onClick={() => {
                AddCard(el)
            }} className="fas fa-plus fa-2x"/>
        }
    }
    return (
        <Container onMouseLeave={() => setHovered(false)} onMouseEnter={() => setHovered(true)}>
            {hovered && (
                <div>
                    {Heart()}
                    {ShopCards()}
                </div>
            )}
            <div className="card border-0 bg-transparent">
                <div className="card-header border-0">
                    <img className="image" src={el.image} alt="Photo"/>
                </div>
                <div className="card-body border-0 d-flex align-items-center justify-content-between">
                    <h4>{el.name}</h4>
                    <p>{el.price}.00$</p>
                </div>
            </div>
        </Container>
    );
};

const Container = styled.div`
  position: relative;

  .fa-shopping-cart {
    color: red;
    position: absolute;
    top: 30px;
    left: 30px;
    z-index: 2 !important;
  }

  .fa-plus {
    color: red;
    position: absolute;
    top: 30px;
    left: 30px;
    z-index: 2 !important;
  }

  .fa-heart {
    position: absolute;
    top: 30px;
    right: 30px;
    z-index: 2 !important;
    color: red;
  }

  .card {
    .card-header {
      overflow: hidden;
      border-radius: 10px;

      .image {
        width: 100%;
        height: 300px;
        object-fit: cover;
        transition: 0.6s;
        border-radius: 10px;

        :hover {
          transform: scale(1.2);
        }
      }
    }

    .card-body {
      h4 {
        text-transform: capitalize;
      }
    }
  }
`

export default HeaderProductsDetail;